//
// Component: Table
//

.table {
  &:not(.table-dark) {
    color: inherit;
  }

  // fixed table head
  &.table-head-fixed {
    thead tr:nth-child(1) th {
      background-color: $white;
      border-bottom: 0;
      box-shadow: inset 0 1px 0 $table-border-color, inset 0 -1px 0 $table-border-color;
      position: sticky;
      top: 0;
      z-index: 10;
    }

    &.table-dark {
      thead tr {
        &:nth-child(1) th {
          background-color: $table-dark-bg;
          box-shadow: inset 0 1px 0 $table-dark-border-color, inset 0 -1px 0 $table-dark-border-color;
        }
      }
    }
  }

  // no border
  &.no-border {
    &,
    td,
    th {
      border: 0;
    }
  }

  // .text-center in tables
  &.text-center {
    &,
    td,
    th {
      text-align: center;
    }
  }

  &.table-valign-middle {
    thead > tr > th,
    thead > tr > td,
    tbody > tr > th,
    tbody > tr > td {
      vertical-align: middle;
    }
  }

  .card-body.p-0 & {
    thead > tr > th,
    thead > tr > td,
    tbody > tr > th,
    tbody > tr > td {
      &:first-of-type {
        padding-left: map-get($spacers, 4);
      }

      &:last-of-type {
        padding-right: map-get($spacers, 4);
      }
    }
  }
}

// Expandable Table
.expandable-body {
  td {
    padding: 0;
    width: 100%;

    > div,
    > p {
      padding: $table-cell-padding;
    }
  }
}

.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
  background: $primary;
  color: $white;
}

.dataTables_wrapper a.btn {
  min-width: 40px;
  padding: 10px;
  height: 100%;

  i {
    font-size: 15px;
  }
}

.filter-bar {
  margin-bottom: 5%;
}

.table {
  margin: 1rem 0 !important;
}

.campaign-detail i {
  font-size: 16px;
  padding-right: 10px;
  cursor: pointer;
  color: $primary;
}

.filter-action {
  background: transparent;
  color: #1a1a1a;
  border: none;
}

.filter-action:hover {
  background: transparent;
  border: none;
  color: #1a1a1a;
  text-decoration: underline;
}

.filter-action.collapsed:before {
  content: 'pokaż opcje';
  display: block;
}

.filter-action:before {
  content: 'zwiń opcje';
  display: block;
}

.hidden {
  visibility: hidden;
}

.tooltip-table {
  color: #ffffff;
}

.popover-mark {
  margin: 6px;
}
